input {
    border: auto;
    outline: none;
    border: 1px solid #000;
    border-radius: 5px;
}

button {
    font-size: 18px;
    background-color: #fff;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 2px 4px rgba(0,0,0,0.5);
    padding: 3px 5px;
    cursor: pointer;
}